// 引入axios
import axios from 'axios'

export function request(config) {
    const instance = axios.create({
        // baseURL: 'http://localhost:3000',
        baseURL: 'https://yxcr-music-api.vercel.app/',
        changeOrigin: true,  //解决跨域
        timeout: 5000
    })
    return instance(config)
}