<template>
    <div class="infinite-list-wrapper">
        <ul
        class="list">
        <li v-for="(music,index) in musicdetails" :key="index" class="list-item" @click="playing(index)">
          <img :src='music.al.picUrl' alt="暂无图片">
          <div class="navname">
              <div>
                  <p class="musicname">{{music.name}}</p>
                  <span class="ar">
                      <p v-for="(ar,i) in music.ar" :key="i" class="pp" @click.stop="artist(ar.id)">{{ar.name}}</p>
                  </span>
              </div>
          </div>
          <div class="centername">
              <div>
                  <p >{{music.al.name}}</p>
              </div>
          </div>
          <div class="time">
            <div>
              <p>{{dt(music.dt)}}</p>
            </div>
          </div>
        </li>
        </ul>
    </div>
</template>

<script>

export default {
    name:'Lists',
    props:['musicdetails'],
    data() {
        return {
        }
    },
    methods: {
        // 格式化时间
        dt(value) {
            if (!value) return ''
            let val = Math.floor(value/1000)
            let min = ~~((val / 60) % 60)
            let sec = (~~(val % 60)).toString().padStart(2, '0')
            return `${min}:${sec}`
        },
        playing(index) {
            // 给点击了的歌添加背景颜色
            // this.$store.commit('INDEX',{index,id:this.id})
            // this.idx = index
          // 用户点击歌曲后发请求判断歌曲是否能播放
          // check(id).then((res) => {
          //   console.log(res)
          // });
          let n = []
          for (let i = 0; i < this.musicdetails.length; i++) {
          n[i] = this.musicdetails[i].id
          }
          this.ids = n.join(',')
          // console.log(n) 
          this.$bus.$emit('playings',n)
          // 全局事件总线进行兄弟组件之间的通信
          this.$bus.$emit("playing",index)
        },
        artist(id) {
          this.$router.push(`/Artistshome/${id}`)
        }
    },
    created() {
      // this.$bus.$on('index',(index,id  ) =>{
      //   console.log(index,'@@@@@@@',id,'@@@@@@@',this.id)
      //   if(this.id == id) {
      //     this.idx = index
      //   }
      // })
    },
    beforeDestroy() {
      // this.$bus.$off('index')
    },
    
}
</script>

<style lang="less" scoped>
  .infinite-list-wrapper {
    border-radius: 12px;
    width:100%;
    overflow-x:hidden;
  }
   .list {
    position: relative;
    z-index: 1;
  }
  .list::before {
    content: '';
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    filter: drop-shadow(0px 20px 10px rgb(255, 255, 255));
    -webkit-filter: blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
  }
  .list li {
    display: flex;
    align-items: center;
    padding: 0.8rem;
    border-radius: 1.2rem;
    cursor: pointer;
  }
  .list li img {
    width: 50px;
    height: 50px;
    border-radius: 10px;
    margin-right: 2rem;
  }
  .list-item:hover {
    background-color: rgba(0, 0, 0, 0.28);
  }
  .pp {
    margin-right:3px;
    word-break:break-all;
    text-overflow:ellipsis;
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp:1 ; /** 显示的行数 **/
    overflow: hidden;  /** 隐藏超出的内容 **/
  }
  .pp:hover {
    color: skyblue;
  }
  .navname {
    width: 30%;
    margin-right: 10%;
  }
  .musicname {
    font-size: 1.8rem;
    font-weight: 600;
    word-break:break-all;
    text-overflow:ellipsis;
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp:1 ; /** 显示的行数 **/
    overflow: hidden;  /** 隐藏超出的内容 **/
  }
  .ar {
    display:flex;
    flex-direction:row;
  }
  .centername {
    width: 52.1%;
    font-size:1.6rem;
  }
  .centername div p {
    word-break:break-all;
    text-overflow:ellipsis;
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp:1 ; /** 显示的行数 **/
    overflow: hidden;  /** 隐藏超出的内容 **/
  }
  .time {
      width: 3.9%;
  }
</style>