<template>
  <div class="first">
      <p style="font-size:2.2rem;margin-top:30px;">推荐艺人</p>
      <artists :artists='artists'></artists>
      <span class="recommend">推荐歌单</span>
      <song-listdetails :result="result"></song-listdetails>
  </div>
</template>

<script>
// 导入子组件
import SongListdetails from '../components/Song-listdetails.vue'

// 导入请求模块
import {artists} from '../network/first.js'

import {listdetails} from '../network/listdetails.js'
import Artists from '../components/Artists.vue'

export default {
  name:'NavFirst',
  data() {
    return {
      artists:[],
      result:[],
    }
  },
  components: {
    SongListdetails,
    Artists,
  },
  created() {
    let offset = parseInt(Math.random() * 30)
    artists(offset).then((res) => {
      this.artists = res.data.artists
    }),
    listdetails(30).then((res) => {
      this.result = res.data.result
    })
  },
}

</script>

