<template>
<div class="login">
    <div class="container">
    <div class="welcome">
        <div class="pinkbox" ref="pinkbox">
        <!-- 注册 -->
        <div class="signup nodisplay" ref="up">
            <h1>Sign In</h1>
            <form autocomplete="off">
            <img :src="qrCodeImage" alt="">
            <span>{{qrCodeInformation}}</span>
            <button class="button submit">Login</button>
            </form>
        </div>

        <!-- 登录 -->
        <div class="signin" ref="in">
            <h1>Sign In</h1>
            <form class="more-padding" autocomplete="off">
            <input type="text" placeholder="Username" v-model="phone">
            <input type="password" placeholder="Password" v-model="key">
            <div class="checkbox">
                <input type="checkbox" id="remember" /><label for="remember">Remember Me</label>
            </div>
            <button class="button sumbit" @click="enter([phone,key,$router])">Login</button>
            </form>
        </div>
        </div>

        <div class="leftbox">
            <button class="btn" id="signin" @click="signin">手机号密码登录</button>
        </div>

        <div class="rightbox">
            <button class="btn" id="signup" @click="signup">二维码登录</button>
        </div>
    </div>
    </div>
</div>
</template>

<script>
import QRCode from 'qrcode';

import {loginQrCodeKey,loginQrCodeCheck} from '../network/login.js'
import {userAccount} from '../network/user.js'

import {mapState,mapActions} from 'vuex'

export default {
    name:'Login',
    data() {
        return {
            phone:null,
            key:'',
            qrCodeImage:'',
            qrCodeKey:'',
            qrCodeInformation:'',
        }
    },
    computed: {
      ...mapState(['loginMessage'])
    },
    created() {
        // 退出创建登录页面时
        this.$store.commit('login',null)
        
    },
    methods: {
        getQrCodeKey() {
            return loginQrCodeKey().then(result => {
                if (result.data.data.code === 200) {
                this.qrCodeKey = result.data.data.unikey;
                QRCode.toDataURL(
                    `https://music.163.com/login?codekey=${this.qrCodeKey}`,
                    {
                    width: 192,
                    margin: 0,
                    color: {
                        dark: '#335eea',
                        light: '#00000000',
                    },
                    }
                )
                    .then(url => {
                    this.qrCodeImage = url;
                    })
                    .catch(err => {
                    console.error(err);
                    })
                }
                this.checkQrCodeLogin();
            });
        },
        checkQrCodeLogin() {
        this.qrCodeCheckInterval = setInterval(() => {
            if (this.qrCodeKey === '') return;
            loginQrCodeCheck(this.qrCodeKey).then(result => {
            if (result.data.code === 800) {
                this.getQrCodeKey(); // 重新生成QrCode
                this.qrCodeInformation = '二维码已失效，请重新扫码';
            } else if (result.data.code === 802) {
                this.qrCodeInformation = '扫描成功，请在手机上确认登录';
            } else if (result.data.code === 801) {
                this.qrCodeInformation = '打开网易云音乐APP扫码登录';
            } else if (result.data.code === 803) {
                clearInterval(this.qrCodeCheckInterval);
                this.qrCodeInformation = '登录成功，请稍等...';
                result.data.cookie = result.data.cookie.replace('HTTPOnly', '');
                userAccount(result.data.cookie).then(res =>{
                    if(res.data.account) {
                        this.$store.commit('Login',{
                            name:res.data.profile.nickname,
                            cookie:result.data.cookie,
                            id:res.data.account.id,
                            avatarUrl:res.data.profile.avatarUrl,
                        })
                        this.$router.push('/Nav-storeroom')
                    }else{
                        alert('登录失败，请重新登录！')
                    }
                })
            }
            });
        }, 1000);
        },

        signin() {
            this.$refs.pinkbox.style.transform = 'translateX(0%)'
            this.$refs.up.classList.add('nodisplay')
            this.$refs.in.classList.remove('nodisplay')
            clearInterval(this.qrCodeCheckInterval);
        },
        signup() {
            this.$refs.pinkbox.style.transform = 'translateX(80%)'
            this.$refs.in.classList.add('nodisplay')
            this.$refs.up.classList.remove('nodisplay')
            this.getQrCodeKey()
        },
        // loginMessage() {
        //     console.log(this.loginMessage)
        // },
        ...mapActions(['enter'])
    }
}
</script>



<style scoped lang="less">

.login {
    width: 80%;
    margin: 0 auto;
}

/* 容器的样式 */
.container {
  margin: auto;
  width: 650px;
  height: 550px;
  position: relative;
}

.welcome {
  background: #f6f6f6;
  width: 650px;
  height: 415px;
  position: absolute;
  top: 25%;
  display: flex;
  flex-direction: row;
  border-radius: 5px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
}

.pinkbox {
  position: absolute;
  top: -10%;
  left: 5%;
  background: #eac7cc;
  width: 320px;
  height: 500px;
  border-radius: 5px;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
  transition: all 0.5s ease-in-out;
  z-index: 2;
}

.nodisplay {
  display: none;
  transition: all 0.5s ease;
}

.leftbox, .rightbox {
  width: 50%;
  height: 100%;
  transition: 1s all ease;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* 字体和按钮的样式 */
h1 {
  font-family: "Open Sans", sans-serif;
  text-align: center;
  margin-top: 95px;
  text-transform: uppercase;
  color: #f6f6f6;
  font-size: 2em;
  letter-spacing: 8px;
}

// p {
//   font-family: "Open Sans", sans-serif;
//   font-size: 0.7em;
//   letter-spacing: 2px;
//   color: #8e9aaf;
//   text-align: center;
// }
.btn {
    width: 108px;
    height: 56px;
      background: #f6f6f6;
  color: #ce7d88;
  border: solid 1px #eac7cc;
    padding: 12px;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 11px;
  border-radius: 10px;
  margin: auto;
  outline: none;;
}

button {
  padding: 12px;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 11px;
  border-radius: 10px;
  margin: auto;
  outline: none;
  display: block;
}

button:hover {
  background: #eac7cc;
  color: #f6f6f6;
  transition: background-color 1s ease-out;
}

.button {
  margin-top: 3%;
  background: #f6f6f6;
  color: #ce7d88;
  border: solid 1px #eac7cc;
}

/* 表单样式 */
form {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 7px;
}

.more-padding {
  padding-top: 35px;
}

.more-padding input {
  padding: 12px;
}

.more-padding .sumbit {
  margin-top: 45px;
}

.sumbit {
  margin-top: 25px;
  padding: 12px;
  border-color: #ce7d88;
}

.sumbit:hover {
  background: #cbc0d3;
  border-color: #bfb1c9;
}

input {
  background: #eac7cc;
  width: 65%;
  color: #ce7d88;
  border: none;
  border-bottom: 1px solid rgba(246, 246, 246, 0.5);
  padding: 9px;
  font-weight: 100;
}

input::placeholder {
  color: #f6f6f6;
  letter-spacing: 2px;
  font-size: 1.0em;
  font-weight: 100;
}

input:focus {
  color: #ce7d88;
  outline: none;
  border-bottom: 1.2px solid rgba(206, 125, 136, 0.7);
  font-size: 1.0em;
  transition: 0.8s all ease;
}

input:focus::placeholder {
  opacity: 0;
}

label {
  font-family: "Open Sans", sans-serif;
  color: #ce7d88;
  font-size: 0.8em;
  letter-spacing: 1px;
}

.checkbox {
  display: inline;
  white-space: nowrap;
  position: relative;
  left: -52px;
  top: 25px;
}

input[type=checkbox] {
  width: 15px;
  background: #ce7d88;
}

.checkbox input[type=checkbox]:checked + label {
  color: #ce7d88;
  transition: 0.5s all ease;
}
</style>