import { request } from './request'

export function songurl(id) {
    return request({
        url: `/song/url?id=${id}`
    })
}
export function songdetail(id) {
    return request({
        url: `/song/detail?ids=${id}`
    })
}
export function lyric(id) {
    return request({
        url: `/lyric?id=${id}`
    })
}