//     })
// }

// 获取网友精选碟
export function findcat() {
    return request({
        url: '/top/playlist'
    })
}

//根据传入tag获取歌单
export function findlist(tag) {
    return request({
        url: `/top/playlist?&order=hot&cat=${tag}`
    })
}

export function highquality() {
    return request({
        url: `/top/playlist/highquality?limit=50`
    })
}