<template>
  <div class="main">
    <div class="head">
      <img :src="coverImgUrl" alt="">
      <ul class="describe">
        <li style="font-size: 3.6rem;font-weight: 700; color:black;">{{name}}</li>
        <li style="font-size: 1.8rem; margin-top: 24px;">Playlist by {{nickname}}</li>
        <li style="font-size: 1.5rem; margin-top: 2px;">最后更新于 2021年11月21日 · {{length}} 首歌</li>
        <li class="description">{{description}}</li>
        <li>???????</li>
      </ul>
    </div>
    <lists :musicdetails="musiclist" :id="id"></lists>
    <div class="btn">
      <button v-if="show" @click="load" @mousedown="clicks($refs.btn)" ref="btn">加载更多</button>
      <button v-if="!show">没有了</button>
    </div>
  </div>
</template>

<script>

import {playlist,musiclist} from '../network/musiclist.js'

import Lists from './Lists.vue'
// import {mapState} from 'vuex'

export default {
    name:'Music-list',
    components:{Lists},
    data () {
      return {
        name:'',
        coverImgUrl:'',
        description:'',
        length:'',
        trackIds:[],
        id:-1,
        ids:null,
        musicdetails:[],
        nickname:'',
        musiclist:[],
        i:-1,
        begin:0,
        end:30,
        show:true,
      }
    },
    methods: {
      load() {
        if(this.i>=0) {
          this.musiclist = this.musiclist.concat(this.musicdetails.slice(this.begin,this.end))
          this.begin = this.end
          this.end += 30
          this.i--
          // 更新play歌曲
          if(this.begin !== 30) {
            let n = []
            for (let i = 0; i < this.musiclist.length; i++) {
              n[i] = this.musiclist[i].id
            }
            this.ids = n.join(',')
            this.$bus.$emit('playings',n)
            n = null
          }
        }else {
          this.show = false
        }
      },
    },
    created() {
      this.id = this.$route.params.id
      playlist(this.$route.params.id).then((res) => {
        this.name = res.data.playlist.name
        this.coverImgUrl = res.data.playlist.coverImgUrl
        this.description = res.data.playlist.description
        this.length = res.data.playlist.trackIds.length
        this.trackIds = res.data.playlist.trackIds
        this.nickname = res.data.playlist.creator.nickname
        // console.log(this.trackIds)
        // console.log('@@@',res)
        // console.log(this)
        var n = []
        for (let i = 0; i < this.trackIds.length; i++) {
        n[i] = this.trackIds[i].id
        }
        this.ids = n.join(',')
        // console.log(this.idss)
        this.$bus.$emit('playings',n)
        
        musiclist(this.ids).then((res) => {
          this.musicdetails = res.data.songs
          this.i = parseInt(this.musicdetails.length / 30)
          this.load()
        })
      })
      
    },
}
</script>

<style scoped>
   /* ::-webkit-scrollbar {
     width: 0 !important;
   }
   ::-webkit-scrollbar {
     width: 0 !important;height: 0;
   } */
  .main {
    width: 80%;
    margin:2rem auto 0;
  }
  .head {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 3rem;
    margin-bottom: 6.6rem;
    position: relative;
    z-index: 1;
  }
  .head::before {
    content: '';
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    filter: drop-shadow(0px 20px 10px rgb(255, 255, 255));
    -webkit-filter: blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
  }
  .describe {
    display: flex;
    flex-direction: column;
  }
  .description {
    font-size: 1.5rem;
    margin-top: 24px;
    cursor: pointer;
    overflow:hidden;
    word-break:break-all;
    text-overflow:ellipsis;
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp:2 ; /** 显示的行数 **/
    overflow: hidden;  /** 隐藏超出的内容 **/
  }
  .head img {
    width:24%;
    border-radius:10px;
    margin-right: 58px;
  }
  .btn {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .btn button {
    width: 108px;
    height: 50px;
    cursor: pointer;
    background-color: transparent;
    border-radius: 10px;
    padding: 8px 16px;
    font-size: 18px;
    font-weight: 600;
    color: white;
  }
</style>