<template>
  <div class="albumshome">
      <div class="album">
        <img :src="album.blurPicUrl || album.picUrl" alt="">
        <div class="intro">
          <h1>{{album.name}}</h1>
          <h4>{{album.type}} by{{artist.name}}</h4>
          <span>2021年 ·  {{album.size}} 首歌 </span>
          <p>{{album.briefDesc}}</p>
          <div>????????</div>
        </div>
      </div>
      <div class="songs">
        <lists :musicdetails="songs" :id="id"></lists>
      </div>
      <p style="opacity:0.6;">发布于{{album.company}}</p>
      <hr>
      <div class="albums">
        <h3>more by {{artist.name}}</h3>
        <albums :result="hotAlbums" @click.native="load"></albums>
      </div>
  </div>
</template>

<script>

import Lists from '../components/Lists.vue'
import Albums from '../components/Albums.vue'

import {albums,artistalbum} from '../network/albumshome.js'



export default {
    name:'Albumshome',
    components:{Lists,Albums},
    data() {
      return {
        id:-1,
        album:{},
        artist:{},
        songs:[],
        hotAlbums:[],
      }
    },
    computed: {
    },
    inject:['reload'],
    methods: {
      load() {
        // this.$router.go(0)
        this.reload()
    }
    },
    created() {
      this.id = this.$route.params.id
      albums(this.$route.params.id).then((res) =>{
        this.album = res.data.album
        this.artist = res.data.album.artist
        this.songs = res.data.songs
        artistalbum(this.artist.id).then((res) =>{
        // 为了不让专辑主页里面显示本页面对应的专辑，用此方法去掉并随机选取专辑显示
        let id = this.$route.params.id
        let l = res.data.hotAlbums
        l.map((val,index) =>{
          if(val.id == id) {
            l.splice(index,1)
          }
        })
        this.hotAlbums = l
        })
      })
    },
}
</script>

<style scoped>
  .albumshome {
    width: 80%;
    margin: 0 auto;
    margin-top: 20px;
  }
  .album {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .album img {
    width: 24%;
    border-radius: 10px;
    margin-right: 58px;
  }
  .intro {
    display: flex;
    flex-direction: column;
  }
  .intro h1 {
    font-size: 3.6rem;
    font-weight: 700;
    color:black;
  }
  .intro h4 {
    font-size: 1.8rem;
    margin-top: 24px;
  }
  .intro span {
    font-size:1.5rem;
    margin-top:2px;
  }
  .intro p {
    font-size: 15px;
    opacity: 0.6;
    margin-top: 30px;
    cursor: pointer;
    overflow:hidden;
    word-break:break-all;
    text-overflow:ellipsis;
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp:2 ; /** 显示的行数 **/
    overflow: hidden;  /** 隐藏超出的内容 **/
  }
  .songs {
    margin: 66px 0;
  }
  .albums {
    margin-top: 24px;
  }
  .albums h3 {
    font-size: 24px;
  }
</style>