<template>
  <div class="room">
    <div class="navroom">
      <h1><img :src="loginMessage.avatarUrl" alt="">
        <span>{{loginMessage.name}}的音乐库</span>
      </h1>
      <p>我喜欢的音乐</p>
    </div>
    <div class="songs">
        <songs :songs="songs"></songs>
          <div class="btn">
            <button v-if="show" @click="loadsongs" @mousedown="clicks($refs.btn)" ref="btn">加载更多</button>
            <button v-if="!show">没有了</button>
          </div>
    </div>
    <div class="person">
      <div class="head">
        <button ref='btn1' class="button" @mousedown="clicks($refs.btn1)" @click="show1" :class="{'bgc':isshow1}">全部歌单</button>
        <button ref='btn2' class="button" @mousedown="clicks($refs.btn2)" @click="show2" :class="{'bgc':isshow2}">专辑</button>
        <button ref='btn3' class="button" @mousedown="clicks($refs.btn3)" @click="show3" :class="{'bgc':isshow3}">艺人</button>
      </div>
      <div class="content">
        <div v-show="isshow1"><song-listdetails :result="listdetails"></song-listdetails></div>
        <div v-show="isshow2"><albums :result="albums" :v-show="isshow2"></albums></div>
        <div v-show="isshow3"><artists :artists="artists" :v-show="isshow3"></artists></div>
      </div>
    </div>
  </div>
</template>

<script>
import Songs from '../components/Songs.vue'
import SongListdetails from '../components/Song-listdetails.vue'
import Albums from '../components/Albums.vue'
import Artists from '../components/Artists.vue'

import { mapState } from 'vuex'
import { musiclist } from '../network/musiclist.js'
import {userLikedSongsIDs,userPlaylist,likedAlbums,likedArtists} from '../network/user.js'


export default {
    name:'NavStoreroom',
    components: {Songs,SongListdetails,Albums,Artists},
    data() {
      return {
        name:'',
        isshow1:true,
        isshow2:false,
        isshow3:false,
        show:true,
        ids:[],
        songs:[],
        listdetails:[],
        albums:[],
        artists:[],
        i:-1,
        begin:0,
        end:20,
      }
    },
    computed: {
      ...mapState(['loginMessage'])
    },
    methods: {
      loadsongs() {
        if(this.i>=0) {
        this.songs = this.songs.concat(this.songss.slice(this.begin,this.end))
        this.begin = this.end
        this.end += 20
        this.i--
        }else {
          this.show = false
        }
      },
      show1() {
        this.isshow1 = true
        this.isshow2 = false
        this.isshow3 = false
      },
      show2() {
        this.isshow1 = false
        this.isshow2 = true
        this.isshow3 = false
      },
      show3() {
        this.isshow1 = false
        this.isshow2 = false
        this.isshow3 = true
      }
    },
    created() {
      // 获取用户喜欢的歌曲
        userLikedSongsIDs(this.loginMessage.id,this.loginMessage.cookie).then((res) =>{
          let ids = res.data.ids.join(',')
          musiclist(ids).then((res) =>{
            // console.log(res)
            this.songss = res.data.songs
            this.i = parseInt(this.songss.length / 20)
            this.loadsongs()
          })
        }),
        // 获取用户收藏的歌单
        userPlaylist(this.loginMessage.id).then((res) =>{
            let playlist = res.data.playlist
            this.listdetails = playlist.slice(1)
        }),
        // 获取用户收藏的专辑
        likedAlbums(this.loginMessage.cookie).then((res) =>{
            this.albums = res.data.data
        }),
        // 获取用户收藏的艺人
        likedArtists(this.loginMessage.cookie).then((res) =>{
            this.artists = res.data.data
        })
        // subcount().then((res) =>{
        //   console.log(res)
        // })

    },
    mounted() {
      
    },
}
</script>


