<template>
  <div class="find">
    <h1 style="margin-top:20px;font-size:50px">发现</h1>
    <ul class="content">
      <li v-for="(item,index) in title" :key="index" @click="songdetail(item)"><button :class="{'button': item == cname}">{{item}}</button></li>
      <li @click="list"><button>···</button></li>
    </ul>
    <div class="list" v-show="isdown">
      <div v-for="(l,index) in categories" :key="index" class="sublist">
        <div class="min">
          <span >{{l}}</span>
        </div>
        <div class="navlist">
          <div v-for="(sub,i) in subs" :key="i">
            <!-- 当index在v-for指令的标签中时，index表示的是本标签index -->
            <span v-if="sub.category == index" @click="check(sub.name)" class="sub">{{sub.name}}</span>
          </div>
        </div>
      </div>
    </div>
    <song-listdetails :result="result"></song-listdetails>
  </div>
</template>

<script>

import SongListdetails from '../components/Song-listdetails.vue'

import {findlistname,findcat,findlist, highquality,} from '../network/find.js'
import {listdetails} from '../network/listdetails.js'

export default {
    components: { SongListdetails },
    name:'NavFind',
    data() {
      return {
        isdown:false,
        title:['全部','推荐歌单','精品歌单','官方','流行'],
        iss:null,
        index:null,
        result:[],
        categories:[],
        iscolor:false,
        cname:'全部',
      }
    },
    methods: {
      list() {
        this.isdown = !this.isdown
      },
      // 所有歌曲歌单列表
      songdetail(name) {
        // 给点击的给定颜色
        this.cname = name
        if(name == '推荐歌单') {
          listdetails(50).then((res) =>{
            this.result = res.data.result
          })
        }else if(name == '精品歌单') {
          highquality().then((res) =>{
            this.result = res.data.playlists
          })
        }else {
          findlist(name).then((res) => {
            this.result = res.data.playlists
            // console.log(res)
          })
        }
      },
      check(name) {
      let flag = this.title.length
        // 实现添加删除数组
      this.title.map((val,i) =>{
          if(val == name) {
            this.title.splice(i,1)
          }
        })
      if(flag == this.title.length) {
        this.title.push(name)
      }
      },
    },
    created() {
      // 把本地存储的分类也就是历史记录重新展示
      if(localStorage.getItem('title')) {
        this.title = localStorage.getItem('title').split(',')
      }
      findlistname().then((res) => {
        // 获取标题
        this.categories = res.data.categories
        this.subs = res.data.sub
        // console.log(res)
      }),
      // 默认首先展示全部歌曲歌单列表
      findcat().then((res) => {
        this.result = res.data.playlists
        // console.log(res)
      })
    },
    beforeDestroy() {
      // 在组件被销毁是存储用户的音乐分类
      localStorage.setItem('title',this.title)
    },
    
}
</script>
