<template>
<div class="root">
  <div class="nav">
    <div class="navbox">
      <div class="nav-contain">
        <router-link to = '/Nav-first' active-class="active" class="sub-nav"><p @mousedown="clicks($refs.first)" ref="first">首页</p></router-link>
        <router-link to = '/Nav-find' active-class="active" class="sub-nav"><p @mousedown="clicks($refs.find)" ref="find">发现</p></router-link>
        <router-link to = '/Ranking-list' active-class="active" class="sub-nav"><p @mousedown="clicks($refs.list)" ref="list">排行榜</p></router-link>
        <router-link to = '/Nav-storeroom' active-class="active" class="sub-nav"><p @mousedown="clicks($refs.storeroom)" ref="storeroom">音乐库</p></router-link>
        <router-link to = '/Video' active-class="active" class="sub-nav"><p @mousedown="clicks($refs.mv)" ref="mv">MV</p></router-link>
      </div>
      <div class="searchbox">
        <div class="search" ref="ipt">
          <span class="iconfont" @click="search(value)">&#xe60b;</span>
          <input type="text" ref="ipt" placeholder="搜索" @focus="ipt" @blur="iptt" v-model="value" @keyup.enter="search(value)">
        </div>
      </div>
      <div class="login">
        <button class="iconfont" @click="log" @mousedown="clicks($refs.btn1)" ref="btn1">&#xe608;</button>
      </div>
    </div>
  </div>
  <div class="contain">
    <vue-scroll :ops="ops" style="width:100%;">
      <!-- <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive> -->
      <keep-alive include="NavFirst,NavFind,RankingList,video,Search">
            <router-view v-if="isRouterAlive" style="margin-bottom:1vh;"></router-view>
      </keep-alive>
    </vue-scroll>
  </div>
  <Background/>
  <Player/>
</div>
</template>

<script>

import Player from './views/Player.vue'
import Background from './components/Background.vue'

import {logout} from './network/user.js'
// import {throttle} from './assets/throttle.js'

export default {
  components: {Player,Background},
  name:'App',
  data() {
    return {
        value:'',
        named:'',
        isRouterAlive:true,
        ops: {
          vuescroll: {
              sizeStrategy: 'number',
          },
          bar: {
            showDelay: 500,
            onlyShowBarOnScroll: true,
            keepShow: false,
            background: '#c1c1c1',
            opacity: 1,
            hoverStyle: false,
            specifyBorderRadius: false,
            minSize: false,
            size: '6px',
            disable: false,
          },
          scrollPanel: {
            scrollingX:false,
          }
        }
    }
  },
  provide() {
    return {
      reload: this.reload,
      
    }
  },
  methods: {
    reload() {
      this.isRouterAlive = false
      this.$nextTick(function () {
        this.isRouterAlive = true
      })
    },
    ipt() {
      this.$refs.ipt.style.color = '#335eea'
    },
    iptt() {
      this.$refs.ipt.style.color = ''
    },
    search(name) {
      this.$bus.$off('over')
      if(name !== '') {
          if(this.$route.fullPath !== '/Search') {
            this.$router.push(`/Search`)
            this.$bus.$on('over', () => {
              this.$bus.$emit('search',name)
            })
            this.$bus.$emit('search',name)
          }else {
            if(this.named !== name) {
              this.$bus.$emit('search',name)
            }
          }
      }
      this.named = name
    },
    log() {
        if(this.$store.state.loginMessage == null) {
          if(this.$route.path !== '/Login') {
            this.$router.push('/Login')
          }
        }else {
          let log = confirm('确定要退出登录吗？')
          if(log) {
            localStorage.removeItem('login')
            logout(this.$store.state.loginMessage.id).then(() =>{
              if(this.$route.path !== '/Login') {
                this.$router.push('/Login')
              }
            })
          }
        }
    }
  },
  created() {
    // 获取已经存储的登录状态
    let login = localStorage.getItem('login')
    let loginMessage = JSON.parse(login)
    this.$store.commit('Login',loginMessage)
  },
  // beforeDestroy() {
  //   this.$bus.$off('showLyric')
  // },
}
</script>

