import Vue from 'vue'
import Vuex from 'vuex'

import * as actions from './actions';
import * as mutations from './mutations.js'
import getters from './getters.js'

Vue.use(Vuex)

const state = {
    loginMessage: null,
    color: {},
    lyrics:null,
}

export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})