export const Login = (state, loginMessage) => {
    state.loginMessage = loginMessage
        // 把登陆状态保存起来，这样刷新后也可以使用
    let loginobj = JSON.stringify(loginMessage)
    localStorage.setItem('login', loginobj)
}

