<template>
  <div class="artistshome">
      <div class="artist">
        <img :src="artist.img1v1Url || artist.picUrl" alt="暂无图片">
        <div class="intro">
          <h1>{{artist.name}}</h1>
          <span>{{artist.albumSize}}张专辑 ·  {{artist.musicSize}} 首歌 · {{artist.mvSize}} 个 MV</span>
          <p>{{artist.briefDesc}}</p>
          <div>????????</div>
        </div>
      </div>
      <div class="hotSongs">
        <h3>热门歌曲</h3>
        <div class="songs">
          <songs :songs="songs"></songs>
          <div class="btn">
            <button v-if="show" @click="loadsongs" @mousedown="clicks($refs.btn)" ref="btn">加载更多</button>
            <button v-if="!show">没有了</button>
          </div>
        </div>
      </div>
      <div class="albums">
        <h3>歌手专辑</h3>
        <albums :result="albums"></albums>
      </div>
      <div class="relateartists">
        <h3>相似艺人</h3>
        <artists :artists="artists" @click.native="load"></artists>
      </div>
  </div>
</template>

<script>

import Songs from '../components/Songs.vue'
import Albums from '../components/Albums.vue'
import Artists from '../components/Artists.vue'

import { artist,simiartist,artistsalbum} from '../network/artisthome.js'


export default {
    name:'Artistshome',
    components: { Songs,Albums, Artists },
    data() {
      return {
        artist:{},
        artists:[],
        s:[],
        songs:[],
        albums:[],
        show:true,
        i:-1,
        begin:0,
        end:20,
      }
    },
    inject:['reload'],
    methods: {
      load() {
        this.reload()
      },
      loadsongs() {
        if(this.i>=0) {
        this.songs = this.songs.concat(this.songss.slice(this.begin,this.end))
        this.begin = this.end
        this.end += 30
        this.i--
        }else {
          this.show = false
        }
      }
    },
    created() {
      artist(this.$route.params.id).then((res) =>{
        this.artist = res.data.artist
        console.log(this.artist)
        this.songss = res.data.hotSongs
        this.i = parseInt(this.songss.length / 20)
        this.loadsongs()

      })
      // artistdetail(this.$route.params.id).then((res) =>{
      //   console.log('@@',res)
      // })
      simiartist(this.$route.params.id).then((res) =>{
        this.artists = res.data.artists
      })
      // artistdesc(this.$route.params.id).then((res) => {
      //   console.log(res)
      // })
      artistsalbum(this.$route.params.id).then((res) => {
        this.albums = res.data.hotAlbums
        // console.log(res)
      })
    },
}
</script>

<style scoped>
  .artistshome {
    width: 80%;
    margin: 0 auto;
  }
  .artist {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 24px;
    position: relative;
    z-index: 1;
  }
  .artist::before {
    content: '';
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    filter: drop-shadow(0px 20px 10px rgb(255, 255, 255));
    -webkit-filter: blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
  }
  .artist img {
    width: 240px;
    height: 240px;
    border-radius: 50%;
    margin-right: 58px;
  }
  .intro {
    display: flex;
    flex-direction: column;
    color:  black;
  }
  .intro h1 {
    font-size: 50px;
    font-weight: 700;
  }
  .intro span {
    font-size: 15px;
    opacity: 0.6;
    margin-top: 30px;
  }
  .intro span:hover {
    opacity: 0.8;
  }
  .intro p {
    font-size: 15px;
    opacity: 0.6;
    margin-top: 30px;
    cursor: pointer;
    overflow:hidden;
    word-break:break-all;
    text-overflow:ellipsis;
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp:2 ; /** 显示的行数 **/
    overflow: hidden;  /** 隐藏超出的内容 **/
  }
  .intro p:hover {
    opacity: 0.8;
  }
  .intro div {
    margin-top: 30px;
  }
  .hotSongs {
    margin-top: 24px;
    width: 100%;
    overflow: hidden;
  }
  .songs {
    overflow: auto;
  }
  .btn {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .btn button {
    height: 39px;
    cursor: pointer;
    background-color: transparent;
    border-radius: 10px;
    padding: 8px 16px;
    font-size: 18px;
    font-weight: 600;
    color: white;
  }
  h3 {
    font-weight: 600;
    font-size: 24px;
    opacity: .88;
    margin-bottom: 16px;
    padding-top: 46px;
  }
</style>