import { login, verify } from '../network/login.js'

export const enter = ({ commit }, message) => {

    verify(message[0]).then((res) => {
        if (res.data.exist == -1) {
            alert('手机号未注册或不存在')
        }
        if (res.data.exist == 1) {
            login(message[0], message[1]).then((res) => {
                if (res.data.code >= 200 && res.data.code < 300) {
                    commit('Login', {
                        name: res.data.profile.nickname,
                        id: res.data.account.id,
                        cookie: res.data.cookie,
                        avatarUrl: res.data.profile.avatarUrl,
                    })
                    message[2].push('/Nav-storeroom')
                } else {
                    alert('密码错误')
                }
            })
        }

    })


}