export default {
    lyrics(state) {
        let lyrics = state.lyrics.lrc.lyric.split('\n')
        let lyric = []
        let time = []
        for (let i = 0; i < lyrics.length; i++) {
            if (lyrics[i].indexOf(']') !== -1) {
                lyric[i] = lyrics[i].split(']')[1]
                time[i] = lyrics[i].split(']')[0] + ']'
            }
        }
        return {
            lyric,
            time
        }
    }
}