<template>
  <div id="rankinglist">
    <div class="span">
        <span>官方榜单</span>
    </div>
    <ul class="listbox">
        <li v-for="(item,index) in navlist" :key="index">
            <Ranking :ranking="item" />
        </li>
    </ul>
    <div class="span">
        <span>全球榜</span>
    </div>
    <song-listdetails :result="list"></song-listdetails>
  </div>
</template>

<script>
import Ranking from '../components/Ranking.vue'
import SongListdetails from '../components/Song-listdetails.vue'

import {ranking} from '../network/ranking.js'

export default {
    name:'RankingList',
    components:{
        Ranking,
        SongListdetails
    },
    data() {
        return {
            navlist:[],
            list:[],
        }
    },
    created() {
        ranking().then((res) =>{
            this.list = res.data.list
            this.navlist = this.list.splice(0,4)
        })
    },
}
</script>

<style scoped>
    #rankinglist {
        width: 80%;
        height: 100%;
        margin: 0 auto;
    }
    .listbox {
        margin: 20px 0;
        display: flex;
        flex-direction: column;

    }
    .listbox li {
        margin: 10px 0;
    }
    .span {
        width: 100%;
        font-size: 22px;
        margin-top: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>