<template>
  <div class="songs">
    <ul class="list">
        <li v-for="(song,index) in songs" :key="index" @click="playing(index)" :class="{'bgc':index ==idx}">
            <img :src="song.al.picUrl" alt="暂无图片">
            <div class="name">
                <div class="subname">
                    <div class="sname">
                        {{song.name}}
                    </div>
                    <div class="aname">
                        <span v-for="(ar,index) in song.ar" :key="index" class="ar">{{ar.name}}</span>
                    </div>
                </div>
            </div>
        </li>
    </ul>
  </div>
</template>

<script>
export default {
    name:'Songs',
    data() {
        return {
            idx:-1,
        }
    },
    props:['songs','likesong'],
    computed:{
    },
    methods: {
        playing(index) {
            // 改变焦点背景
            this.idx = index
            var n = []
            for (let i = 0; i < this.songs.length; i++) {
                n[i] = this.songs[i].id
            }
            this.$bus.$emit('playings',n)
            // 全局事件总线进行兄弟组件之间的通信
            this.$bus.$emit("playing",
                index
            )
        }
    },
    created() {
        // this.$bus.$on('index',(data) =>{
        //     this.idx = data
        // })
    },
    beforeDestroy() {
        // this.$bus.$off()
    },
}
</script>

<style lang="less" scoped>
    .songs::before {
        content: '';
        z-index: -1;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        filter: drop-shadow(0px 20px 10px rgb(255, 255, 255));
        -webkit-filter: blur(20px);
        backdrop-filter: saturate(180%) blur(20px);
    }
    .list {
        display: grid;
        grid-template-columns: repeat(4,1fr);
        row-gap: 4px;
        column-gap: 4px;
        width: 100%;
    }
    .list li {
        display: flex;
        flex-direction: row;
        padding: 8px;
        border-radius: 10px;
        align-items: center;
    }
    .list li:hover {
        background-color: rgb(192, 192, 192);
    }
    /* .bgc {
        background-color: rgb(167, 255, 233);
    } */
    .list li img {
        height: 50px;
        width: 50px;
        border-radius: 6px;
        margin-right: 14px;
        cursor: pointer;
    }
    .name {
        display: flex;
        flex-direction: row;
        flex-grow: 1;
        flex-shrink: 1;
        flex-basis: 0%;
    }
    .subname {
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .sname {
        font-size: 16px;
        font-weight: 600;
        cursor: default;
        word-break:break-all;
        text-overflow:ellipsis;
        display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
        -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
        -webkit-line-clamp:1 ; /** 显示的行数 **/
        overflow: hidden;  /** 隐藏超出的内容 **/
    }
    .aname {
        word-break:break-all;
        text-overflow:ellipsis;
        display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
        -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
        -webkit-line-clamp:1 ; /** 显示的行数 **/
        overflow: hidden;  /** 隐藏超出的内容 **/
    }
    .ar {
        margin-right:10px;
        cursor: pointer;
    }
</style>