import Vue from "vue";

import Router from 'vue-router'

Vue.use(Router)


// 导入组件
import NavFirst from '../views/Nav-first.vue'
import NavFind from '../views/Nav-find.vue'
import NavStoreroom from '../views/Nav-storeroom.vue'
import Login from '../views/Login.vue'
import Albumshome from '../views/Albums-home.vue'
import Artistshome from '../views/Artists-home.vue'
import Lyrics from '../views/Lyrics.vue'
import RankingList from '../views/Ranking-list.vue'
import Video from '../views/Video.vue'

import MusicList from '../components/Music-list.vue'
import Search from '../components/Search.vue'



const router = new Router({
    routes: [{
            path: '/',
            redirect: '/Nav-first'
        },
        {
            path: '/Nav-first',
            component: NavFirst,
        },
        {
            path: '/Nav-find',
            component: NavFind,
        },
        {
            path: '/Nav-storeroom',
            component: NavStoreroom,
            meta: {
                requireAuth: true,
                type: ['登录']
            },
        },
        {
            path: '/Music-list/:id',
            component: MusicList
        },
        {
            path: '/Search',
            component: Search
        },
        {
            path: '/Login',
            component: Login,
            meta: {
                // requireAuth: true,
            }
        },
        {
            path: '/Albumshome/:id',
            component: Albumshome,
        },
        {
            path: '/Artistshome/:id',
            component: Artistshome,
            // meta: {
            //     keepAlive: true,
            // }
        },
        {
            path: '/Lyrics',
            component: Lyrics,
            meta: {
                color:true
            }
        },
        {
            path: '/Ranking-list',
            component:RankingList,
        },
        {
            path: '/Video',
            component:Video,
        }
    ]
})

// 路由守卫进行拦截判断是否登录
router.beforeEach((to, from, next) => {
    if (to.meta.requireAuth) {
        if (router.app.$store.state.loginMessage !== null) {
            next()
        } else if (from.path !== '/Login') {
            router.push('/Login')
        }
    } else if (from.meta.color) {
        // router.app.$refs.lyric.style.color = 'white'
        router.apps[0].$bus.$emit('color')
        next()
    }else {
        next()
    }
})

export default router