<template>
  <div class="search-content">
      <div class="people">
        <span style="margin-right:30px">
            <p style="font-size:22px">艺人</p>
            <ul>
                <li v-for="(ar,index) in artists" :key="index" class="box" @click="artist(ar.id)">
                        <img :src="ar.img1v1Url" alt="" style="border-radius: 50%;">
                    <p>{{ar.name}}</p>
                </li>
            </ul>
        </span>
        <span>
            <p style="font-size:22px">专辑</p>
            <ul>
                <li v-for="(al,index) in albums" :key="index" class="box" @click="album(al.id)">
                    <div class="subbox">
                        <img :src="al.blurPicUrl" alt="" style="border-radius: 5%;position:absolute;top:0;">
                    </div>
                    <p>{{al.name}}</p>
                    <span style="text-align:center;cursor: pointer;">{{al.artist.name}}</span>
                </li>
            </ul>
        </span>
      </div>
      <div>
          <h3 style="font-size:22px;">歌曲</h3>
            <songs :songs='songs'></songs>
      </div>
      <div>
          <h3 style="font-size:22px;">相关歌单</h3>
          <song-listdetails :result="result"></song-listdetails>
      </div>
  </div>
</template>

<script>

import Songs from '../components/Songs.vue'
import SongListdetails from './Song-listdetails'

import {search2} from '../network/search'

export default {
    name:'Search',
    components:{Songs,SongListdetails},
    data() {
        return {
            artists:[],
            albums:[],
            songs:[],
            result:[],
        }
    },
    methods: {
        artist(id) {
            this.$router.push(`/Artistshome/${id}`)
        },
        album(id) {
            this.$router.push(`/Albumshome/${id}`)
        }
    },
    created() {
        this.$bus.$on('search',(name) =>{
            // 单曲
            search2(name,1,16).then((res) => {
                this.songs = res.data.result.songs
            }),
            // 专辑
            search2(name,10,6).then((res) => {
                this.albums = res.data.result.albums
            }),
            // 歌手
            search2(name,100,6).then((res) => {
                this.artists = res.data.result.artists
            }),
            // 歌单
            search2(name,1000,10).then((res) => {
                    this.result = res.data.result.playlists
            })
        })
        this.$bus.$emit('over','')
    }, 
    beforeDestroy() {
        this.$bus.$off('search')
    }
}
</script>

<style scoped>
    .search-content {
        position: relative;
        width: 80%;
        margin: 0 auto;
    }
    .people {
        display: flex;
        flex-direction: row;
    }
    .people span {
        width: 50%;
    }
    .people span ul {
        display: grid;
        width: 100%;
        grid-template-columns: repeat(3,1fr);
        grid-template-rows: repeat(2,1fr);
        grid-gap: 10px 10px;
    }
    .box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex-grow: 1;
        flex-shrink: 1;
        flex-basis: 0%;
    }
    .box p {
        margin-top:10px;
        font-size:16px;
        cursor: pointer;
    }
    /* 让图片的宽度和高度相等 */
    .subbox {
        width: 100%;
        padding-top: 100%;
        display: inline-block;
        position: relative;
        overflow: hidden;
        border-radius: 5%;
        transition: 0.5s all;
    }
    .subbox:hover {
        box-shadow: 0px 8px 16px rgb(80 80 80);
    }
    .box img {
        width: 100%;
        background: transparent;
        transition: 0.5s all;
    }
    .box img:hover {
        box-shadow: 0px 8px 16px rgb(80 80 80);
    }
</style>