<template>
    <div v-show="show" id="play">
        <div class="player" @click.self="up">
            <div class="playerbox">
                <div class="block" @mousedown="isDrag">
                    <el-slider v-model="value1" :max="dtt" :format-tooltip="formatTooltip" @change="change"></el-slider>
                </div>
                <ul class="play">
                    <li>
                        <img :src="picUrl" alt="" style="height:88%;border-radius:8%; float:left;">
                        <div class="font" style="margin-left:12px">
                            <div style="font-size:16px; margin-bottom: 4px;cursor: pointer;">{{name}}</div>
                            <div style="display:flex;flex-direction:row;">
                                <span v-for="(singer,index) in ar" :key="index">{{singer.name}}</span>
                            </div>
                        </div>
                    </li>
                    <li class="first">
                        <button class="iconfont" @mousedown="clicks($refs.shang)" ref="shang">&#xe603;</button>                      <!-- 上一首 -->
                        <button v-show="!isshow1" class="iconfont" @mousedown="clicks($refs.bofang)" ref="bofang" @click.stop="play" style="font-size:28px">&#xe60f;</button>        <!-- 播放 -->
                        <button v-show="isshow1" class="iconfont" @mousedown="clicks($refs.zantin)" ref="zantin" @click.stop="pause" style="font-size:28px">&#xe6aa;</button>         <!-- 暂停 -->
                        <audio :src="url" autoplay ref="audio" @timeupdate="time" class="audio"></audio>                        <!-- 音乐标签 -->
                        <button class="iconfont" @mousedown="clicks($refs.xia)" ref="xia">&#xe763;</button>                           <!-- 下一首 -->
                    </li>
                    <li class="last">
                        <button class="iconfont" @click="drawer = true" type="primary" @mousedown="clicks($refs.liebiao)" ref="liebiao">&#xe75f;</button>
                        <button class="iconfont" style="color:#6cdaf5;" v-show="listloop" @click="setloop" @mousedown="clicks($refs.xunhuan)" ref="xunhuan">&#xe664;</button>
                        <button class="iconfont" style="color:#6cdaf5;" v-show="!listloop" @click="setloop" @mousedown="clicks($refs.suiji)" ref="suiji">&#xe61a;</button>
                        <span class="volume">
                            <button class="iconfont">&#xe662;</button>
                            <el-slider v-model="value2" @change="setVolume" style="width: 100%;cursor: pointer;"></el-slider>
                        </span>
                        <button style="font-size:25px" class="iconfont" @click="lyrics" @mousedown="clicks($refs.lyric)" ref="lyric">&#xe601;</button>
                    </li>
                </ul>
            </div>
        </div>
        <div class="drawer">
            <el-drawer
                title="播放列表"
                :visible.sync="drawer"
                :with-header="false"
                @opened='open'>
                <div class="playlist">
                    <h3>当前播放</h3>
                    <vue-scroll :ops="ops" style="width:100%;height:100%;">
                        <div class="playlistbox">
                            <lists :musicdetails="musicdetails" style="margin-bottom:5px;width:98%"></lists>
                        </div>
                    </vue-scroll>
                </div>
            </el-drawer>
        </div>
    </div>
</template>

<script>

import Lists from '../components/Lists.vue'

import {songurl,songdetail,lyric} from '../network/songdetail.js'
// import {userLikeSong} from '../network/user.js'

import {musiclist} from '../network/musiclist.js'

export default {
    name:'Player',
    components:{Lists},
    data() {
        return {
            value1:0,
            value2:50,
            show:false,
            isshow1:false,
            url:null,
            picUrl:'',
            name:'',
            ar:'',
            dt:0,
            id:-1,
            ids:null,
            idss:[],
            index:-1,
            isDraging:false,
            drawer: false,
            musicdetails:[],
            listloop:true,
            // au:null
            ops: {
                vuescroll: {
                    sizeStrategy: 'number',
                },
                rail: {
                    gutterOfEnds:"10px"
                },
                bar: {
                    showDelay: 500,
                    onlyShowBarOnScroll: true,
                    keepShow: false,
                    background: '#c1c1c1',
                    opacity: 1,
                    size: '6px',
                    disable: false,
                }
            }
        }
    },
    computed: {
        // 计算属性简写形式，返回一个dtt在vm上，相当于this.dtt，而不是一个函数
        dtt () {
            return Math.floor(this.dt/1000)
        },
    },
    methods: {
        // 音乐播放slider
        isDrag() {
            this.isDraging = true;
            let that = this;
            if(this.isDraging) {
                window.onmouseup = function() {
                    // console.log(this.isshow1)
                    // that.isshow1 = true;
                    that.play()
                    that.isDraging = false;
                    // console.log("hello");
                    // 性能优化解绑事件
                    window.onmouseup = null;
                }
            }
        },
        // 格式化时间
        formatTooltip(val) {
            if (!val) return '';
            let min = ~~((val / 60) % 60);
            let sec = (~~(val % 60)).toString().padStart(2, '0');
            return `${min}:${sec}`;
        },


        lyrics() {
            if(this.$route.fullPath !== '/Lyrics') {
                this.$router.push('/Lyrics')
                this.$refs.lyric.style.color = '#33adea'
            }else {
                this.$router.go(-1)
            }
        },
        // 喜欢音乐
        // like(id) {
        //     if(this.$store.state.loginMessage !==null) {
        //         userLikeSong(id,this.likesong,this.$store.state.loginMessage.cookie).then((res) =>{
        //             console.log(res)
        //             if(res.data.code == 200) {
        //                 this.likesong = !this.likesong
        //             }
        //         })
        //     }else {
        //         alert('请先登录')
        //     }
        // },

        // 播放和暂停音乐
        play() {
            this.isshow1 = true;
            this.$refs.audio.play()
    
        },
        pause() {
            this.isshow1 = false;
            this.$refs.audio.pause()
        },
        // 让音乐循环播放
        loop() {
            this.next()
            // this.time()
        },
        // 让音乐随机播放
        suiji() {
            this.index = Math.floor( Math.random() * this.idss.length )
            this.song()
        },

        setloop() {
            if(this.listloop) {
                this.$refs.audio.removeEventListener('ended',this.loop)
                this.$refs.audio.addEventListener('ended',this.suiji)
                this.$refs.xia.addEventListener('click',this.suiji)
                this.$refs.shang.addEventListener('click',this.suiji)
                this.$refs.xia.removeEventListener('click',this.next)
                this.$refs.shang.removeEventListener('click',this.on)
                this.listloop = false
            }else{
                this.$refs.audio.removeEventListener('ended',this.suiji)
                this.$refs.audio.addEventListener('ended',this.loop)
                this.$refs.xia.removeEventListener('click',this.suiji)
                this.$refs.shang.removeEventListener('click',this.suiji)
                this.$refs.xia.addEventListener('click',this.next)
                this.$refs.shang.addEventListener('click',this.on)
                this.listloop = true
            }
        },

        // 设置声音
        setVolume() {
            this.$refs.audio.volume = this.value2/100
        },
        // volume(v) {
            
        // },

        // 获取音乐信息
        song() {
            songdetail(this.idss[this.index]).then((res) => {
                console.log(res)
                this.picUrl = res.data.songs[0].al.picUrl
                this.name = res.data.songs[0].name
                this.ar = res.data.songs[0].ar
                this.dt = res.data.songs[0].dt
            })
            // 获取音乐播放地址
            songurl(this.idss[this.index]).then((res) => {
            this.url = res.data.data[0].url
            console.log(this.url)
            // 点击歌曲播放
            this.isshow1 = true
            })

            // 获取歌词
            lyric(this.idss[this.index]).then((res) => {
                this.$store.commit('Lyrics',res.data)
                console.log(res.data)
            })
        },
        // 获取歌曲播放更新

        time(res) {
            if (!this.isDraging) {
                // this.value1 = this.formatTooltip(res.target.currentTime)
                this.value1 = res.target.currentTime / this.$refs.audio.duration*this.dtt
                // console.log(this.$refs.audio.duration)
                // console.log(res.target.currentTime)
          }
        },
        change(res) {
            // console.log(this.$refs.audio.duration)
            // this.$refs.audio.currentTime = parseInt(res / 100 * this.audio.maxTime)
            // console.log(res)
            // this.au.target.currentTime = res
            this.$refs.audio.currentTime = this.$refs.audio.duration*res / this.dtt
            this.$refs.audio.play()
        },
        // 上一首
        on() {
            if(this.index > 0){
            --this.index
            // 背景聚焦在正在播放的歌曲
            // this.$bus.$emit('index',this.index,this.id)
            this.song()
            }
            
        },
        // 下一首
        next() {
            if(this.index < this.idss.length - 1){
            ++this.index
            // 背景聚焦在正在播放的歌曲
            // this.$bus.$emit('index',this.index,this.id)
            this.song()
            }
        },
        open() {
            // this.$bus.$emit('index',this.index,this.id)
        }

    },
    created() {
        this.$bus.$on('color',() =>{
            this.$refs.lyric.style.color = 'white'
        })
        this.$bus.$on("playings",(idss) => {
                        // console.log(idss);
            this.idss = idss
            this.ids = idss.join(',')
            musiclist(this.ids).then((res) => {
            this.musicdetails = res.data.songs
            // console.log(res)
        })
        })
        this.$bus.$on("playing", (index) => {
                this.show = true;
                this.index = index
                this.song()
        })
    },
    mounted() {
        window.onload = (() =>{
            this.$refs.audio.addEventListener('ended',this.loop)
            this.$refs.shang.addEventListener('click',this.on)
            this.$refs.xia.addEventListener('click',this.next)
        })
    },
    beforeDestroy() {
        
    },
}
</script>

<style lang="less" scoped >
@import'../assets/font/iconfont.css';
    /deep/ .el-slider__runway {
        height: 2px;
    }
    /deep/ .el-slider__button {
        width: 13px;
        height: 13px;
        border: 0;
        background-image: linear-gradient(to right, rgb(255, 255, 255),rgb(255, 190, 190)); 
    }
    /deep/ .el-slider__button-wrapper {
        display: none;
        top: -16px;
    }
    /deep/ .el-slider__runway:hover .el-slider__button-wrapper {
        display: block;
    }
    /deep/ .el-slider__bar {
        height: 2px;
        background-image: linear-gradient(to right, red,orange,yellow,rgb(135, 255, 135),rgb(125, 125, 255),rgb(134, 251, 255),violet); 
    }

    .player {
        width: 100%;
        height: 8vh;
        color: white;
    }
    .playerbox {
        display: flex;
        justify-content: center;
        align-content: center;
        position: relative;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 99 !important;
    }
    .playerbox::before {
        z-index: -1;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        filter: drop-shadow(0px 20px 10px rgb(145, 145, 145));
        -webkit-filter: blur(50px);
        backdrop-filter: saturate(180%) blur(50px);
    }
    .block {
        position: absolute;
        /* left: -50%; */
        top: -18px;
        width: 100%;
        /* transform: scale(0.5,0.5); */
    }
    .play {
        display: grid;
        grid-template-columns: repeat(3,1fr);
        width: 80%;
        height: 100%;
        padding: 0 8px;
        margin: 0 auto;
    }
    .iconfont {
        cursor: pointer;
        background-color: transparent;
        border: 0;
        color: white;
    }
    .play li:nth-child(1) {
        height: 8vh;
        display: flex;
        flex-direction: row;
        align-items: center;
        cursor: pointer;
    }
    .play li:nth-child(2) {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .play li:nth-child(3) {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    .font > div {
        word-break:break-all;
        text-overflow:ellipsis;
        display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
        -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
        -webkit-line-clamp:1 ; /** 显示的行数 **/
        overflow: hidden;  /** 隐藏超出的内容 **/
    }
    .font div:nth-child(2) span {
        word-break:break-all;
        text-overflow:ellipsis;
        display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
        -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
        -webkit-line-clamp:1 ; /** 显示的行数 **/
        overflow: hidden;  /** 隐藏超出的内容 **/ 
    }
    .first button {
        font-size:18px;
        margin:0 10px;
    }
    .last button {
        font-size:16px;
        margin:0 10px;
    }
    .volume {
        width: 120px;
        display: flex;
        flex-direction: row;
    }
    .playlist {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .playlist h3 {
        background-image: linear-gradient(to bottom right, pink, rgb(91, 202, 230));
        -webkit-background-clip: text;
        -moz-background-clip: text;
        -ms-background-clip: text;
        color: transparent;
        font-size: 25px;
        cursor: pointer;
        margin: 10px 0;
    }
    .playlistbox {
        width: 100%;
        height: 100%;
    }
</style>