export default {
    // 传入vm的构造函数
    install(Vue) {
        // 全局混入
        Vue.mixin({
                // 栗子：
                // data() {
                //     return {
                //       artists:[],
                //       result:[],
                //       banners:[],
                //     }
                //   },
            })
            // 给vue原型添加全局方法
        Vue.prototype.clicks = (element) => {
            element.style.transform = 'scale(0.8,0.8)'
            window.onmouseup = function() {
                element.style.transform = ''
                window.onmouseup = null
            }
        }
    },

}