<template>
  <div id="ranking">
    <div class="rankingbox">
        <img :src="ranking.coverImgUrl" alt="暂无" @click="musiclist(ranking.id)">
        <ul class="middle">
           <li v-for="item in 5" :key="item">{{item}}</li> 
        </ul>
        <div class="right">
            <lists :musicdetails="music"></lists>
        </div>
    </div>
    </div>
</template>

<script>
import {playlist,musiclist} from '../network/musiclist.js'


import Lists from './Lists.vue'
export default {
  components: { Lists },
    name:'Ranking',
    props:['ranking'],
    data() {
        return {
            music:[],
        }
    },
    created() {
        // console.log(this.ranking)
        playlist(this.ranking.id).then((res) =>{
            let n = []
            let trackIds = res.data.playlist.trackIds
            let l = trackIds.length
            if(l> 5) {
                for (let i = 0; i < 5; i++) {
                    n[i] = trackIds[i].id
                }
            }else {
                for (let i = 0; i < l; i++) {
                    n[i] = trackIds[i].id
                }
            }
            this.ids = n.join(',')
            // console.log(this.ids)
            musiclist(this.ids).then((res) =>{
                this.music = res.data.songs
            })
        })
    },
    methods: {
        musiclist(id) {
            this.$router.push(`/Music-list/${id}`)
        }
    },
}
</script>

<style lang="less" scoped>
    #id {
        width: 100%;
        height: 100%;
    }
    .rankingbox {
        display: flex;
        flex-direction: row;
    }
    .rankingbox img {
        height: 330px;
        border-radius: 5%;
        transition: 0.2s all;
    }
    .rankingbox img:hover {
        transform: scale(0.9,0.9);
        box-shadow: 5px 5px 39px black;
    }
    .middle {
        width: 66px;
        height: 330px;
        border: 1px solid transparent;
        display: flex;
        flex-direction: column;
    }
    .middle li {
        width: 50px; 
        height: 50px;
        padding: 8px;
        text-align: center;
        line-height: 50px;
    }
    .right {
        width: 100%;
        height: 100%;
    }
</style>